import { lazy, Suspense, useState } from "react";

const FormSignin = lazy(() => import('../components/FormSignin'));
const FormSignup = lazy(() => import('../components/FormSignup'));

const Auth = () => { 
  const [authForm, setAuthForm] = useState(true);

  return (
    <section className="container"> 
      <div className="auth">
        <button className="auth-link" onClick={() => setAuthForm(true)} aria-label="">
          
        </button>
        <span>  </span>
        <button className="auth-link" onClick={() => setAuthForm(false)} aria-label="">
          
        </button>
        <Suspense fallback={<div>...</div>}>
          {authForm 
            ? <FormSignin />
            : <FormSignup />
          }
        </Suspense>
      </div>
    </section>
  );
}

export default Auth;